  border-radius: 20px;
  transition: width 0.25s ease;
}

.spin-mg {
    /* background-image: url(assets/images/turn.webp) ; */
    background-repeat: no-repeat;
    height: 400px;
}

.bg-img-mg {
    position: relative;
    width: 450px;
    height: 450px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.bg-img-mg .ro360 {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
    animation: rotate360 30s linear infinite;
}

@keyframes rotate360 {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.black-ring {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 350px;
    height: 350px;
    /* background-color: rgba(0, 0, 0, 0.5); */
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;

}

.blackCircle {
    width: 400px;
    height: 400px;

}

.black-ring img,
.blackCircle img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%
}

